@media print {
  /* Reset all margins and padding */
  * {
    margin: 0 !important;
    padding: 0 !important;
    box-sizing: border-box;
  }

  /* Reset body and html */
  html,
  body {
    margin: 0 !important;
    padding: 0 !important;
    height: auto !important;
    background: white;
  }

  /* Hide all elements first */
  body * {
    visibility: hidden;
  }

  /* Show only printable content */
  #printable-area,
  #printable-area * {
    visibility: visible;
  }

  /* Fix the main printable area positioning */
  #printable-area {
    position: absolute !important;
    top: 2% !important;
    width: 98% !important;
    left: 1%;
    max-width: 98% !important;
    padding: 0 !important; /* Remove all padding */
    background: white;
    display: block !important;
    transform: none !important; /* Remove any transforms */
    page-break-after: avoid;
  }

  /* Remove margins from common wrapper elements */
  .container,
  .wrapper,
  .main-content,
  .content-wrapper,
  .page-wrapper {
    margin: 0 !important;
    padding: 0 !important;
    position: static !important;
  }

  /* Print heading - reduce top spacing */
  .printHeading {
    display: block !important;
    text-align: center;
    margin: 5px 0 10px 0 !important; /* Minimal top margin */
    padding: 0 !important;
    font-size: 16px;
    font-weight: bold;
    color: #000 !important;
    page-break-after: avoid;
    line-height: 1.2;
  }

  /* Hide non-essential elements */
  .tableHeaderContainer,
  .grnPrintPopupPrint,
  .hide-print,
  .scoop-left-header,
  .no-print,
  nav,
  header,
  .navbar,
  .sidebar,
  .breadcrumb,
  .pagination,
  button,
  .btn {
    display: none !important;
    visibility: hidden !important;
  }

  /* Page container - ensure no top spacing */
  .pagePrintContainer {
    position: absolute !important;
    width: 100%;
    margin: 0 !important;
    padding: 0 !important;
    top: 0 !important;
  }

  /* Outlet container */
  .outletContainer {
    height: 100% !important;
    min-height: 100% !important;
    overflow: visible !important;
    margin: 0 !important;
    padding: 0 !important;
  }

  /* Force content to start at top */
  .content-start-top {
    margin-top: 0 !important;
    padding-top: 0 !important;
    position: static !important;
    top: 0 !important;
  }

  /* Remove any absolute positioning that might cause gaps */
  .absolute,
  .fixed,
  .relative {
    position: static !important;
    top: auto !important;
    left: auto !important;
    right: auto !important;
    bottom: auto !important;
    transform: none !important;
  }

  /* Footer - minimal spacing */
  .footText,
  .grnPopupFoot {
    color: #000 !important;
    font-weight: 600 !important;
    margin: 10px 0 0 0 !important;
    padding: 0 !important;
    font-size: 16px;
  }

  /* Utility class to force no top spacing */
  .print-no-gap {
    margin-top: 0 !important;
    padding-top: 0 !important;
    position: static !important;
    top: 0 !important;
  }

  /* Ensure first child has no top margin */
  #printable-area > *:first-child {
    margin-top: 0 !important;
    padding-top: 0 !important;
  }

  /* Remove transforms that might cause positioning issues */
  * {
    transform: none !important;
    -webkit-transform: none !important;
    -moz-transform: none !important;
    -ms-transform: none !important;
  }

  .gridTableHeader {
    font-size: 13px !important;
    color: #23b7ec !important;
  }

  .printNetTotalContainer {
    width: 70% !important;
