/* Drawer Container */
.right-drawer {
  position: fixed;
  top: 0;
  right: 0;
  min-width: 800px;
  height: 100vh;
  background-color: #ffffff;
  box-shadow: -4px 0 15px rgba(0, 0, 0, 0.1);
  z-index: 999;
  display: flex;
  flex-direction: column;
  transition: transform 0.3s ease-in-out;
  border-left: 1px solid #e0e0e0;
}

/* Drawer States */
.drawer-open {
  transform: translateX(0);
}

.drawer-closed {
  transform: translateX(100%);
}

/* Drawer Header */
.drawer-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 2px solid #e0e0e0;
  background-color: #f8f9fa;
  min-height: 70px;
}

.drawer-title {
  margin: 0;
  font-size: 30px;
  font-weight: 300;
}

.drawer-close-btn {
  background: white;
  border: none;
  cursor: pointer;
  padding: 8px;
  border-radius: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #666;
  transition: all 0.2s ease;
  position: absolute;
  left: -60px;
}

/* .drawer-close-btn:hover {
  background-color: #e9ecef;
  color: #333;
} */

.drawer-close-btn:focus {
  outline: 2px solid #007bff;
  outline-offset: 2px;
}

/* Drawer Content */
.drawer-content {
  flex: 1;
  padding: 24px;
  overflow-y: auto;
  overflow-x: hidden;
}

/* Custom Scrollbar for Drawer Content */
.drawer-content::-webkit-scrollbar {
  width: 6px;
}

.drawer-content::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

.drawer-content::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

.drawer-content::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* Responsive Design */
@media (max-width: 768px) {
  .right-drawer {
    width: 100vw;
    max-width: 100vw;
  }

  .drawer-header {
    padding: 16px 20px;
    min-height: 60px;
  }

  .drawer-title {
    font-size: 16px;
  }

  .drawer-content {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .drawer-header {
    padding: 12px 16px;
    min-height: 56px;
  }

  .drawer-title {
    font-size: 15px;
  }

  .drawer-content {
    padding: 16px;
  }
}

/* Animation for smooth opening/closing */
@media (prefers-reduced-motion: no-preference) {
  .right-drawer {
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  }

  .drawer-overlay {
    animation: fadeIn 0.3s ease-out;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Prevent body scroll when drawer is open */
body.drawer-open {
  overflow: hidden;
}

/* Focus management */
.right-drawer:focus {
  outline: none;
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .right-drawer {
    border-left: 2px solid #000;
