/* Language DropDown */
/* Hide the default radio */
.youtubeLanguage-custom-radio input[type="radio"] {
  display: none;
}

/* Label acts as the styled radio */
.youtubeLanguage-custom-radio label {
  position: relative;
  cursor: pointer;
  padding-left: 28px;
  line-height: 20px;
  display: inline-flex;
  align-items: center;
  font-size: 15px;
  color: #333;
  user-select: none;
  transition: color 0.2s ease-in-out;
}

/* Outer circle */
.youtubeLanguage-custom-radio label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 18px;
  height: 18px;
  border: 2px solid #3f4d54;   /* brand color */
  border-radius: 50%;
  background: #fff;
  transition: border-color 0.3s ease, background-color 0.3s ease;
}

/* Inner dot */
.youtubeLanguage-custom-radio label::after {
  content: "";
  position: absolute;
  left: 4px;
  top: 50%;
  transform: translateY(-50%) scale(0);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #3f4d54;
  transition: transform 0.2s ease-in-out;
}

/* Checked state */
.youtubeLanguage-custom-radio input[type="radio"]:checked + label::after {
  transform: translateY(-50%) scale(1);
}

.youtubeLanguage-custom-radio input[type="radio"]:checked + label::before {
  border-color: #3f4d54;
}

/* Hover effect */
.youtubeLanguage-custom-radio label:hover {
  color: #3f4d54;
}
.youtubeLanguage-radio-container{
  display: flex;
    gap: 2rem;
    padding-left: 1rem;
    padding-bottom: 1rem;
}

/* Toogle Button Styling */
.toggle-switch-container {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 15px;
}

.toggle-switch-label {
  font-weight: 600;
  color: #333;
  min-width: 210px; /* Adjust as needed for alignment */
}

.toggle-switch {
  width: 60px;
  height: 28px;
  border-radius: 15px;
  cursor: pointer;
  position: relative;
  transition: background-color 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 5px;
}

.toggle-switch.on {
  background-color: #2ab934; /* Green */
}

.toggle-switch.off {
  background-color: #a6adc8; /* Light purple/grey */
}

.toggle-switch-inner-circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: white;
  position: absolute;
  transition: left 0.3s ease;
}

.toggle-switch.on .toggle-switch-inner-circle {
  left: calc(100% - 25px); /* Move to the right */
}

.toggle-switch.off .toggle-switch-inner-circle {
  left: 5px; /* Move to the left */
}

.toggle-switch-text {
  color: white;
  font-size: 10px;
  font-weight: bold;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.toggle-switch.on .toggle-switch-text {
  left: 8px;
}

.toggle-switch.off .toggle-switch-text {
  right: 8px;
}

/* Read-only mode styling */
.toggle-switch.readonly .toggle-switch-text {
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 12px;
  font-weight: bold;
}
